const { game } = require("./app");

let uScore = 0;
let cScore = 0;

const userScore = document.querySelector("#user-score");
const computerScore = document.querySelector("#computer-score");

const result = document.querySelector(".result");

const play = () => {
  rock.addEventListener("click", () => {
    const computer = getComputerChoice();
    const val = game("rock", computer);
    getOutput(val);
  });
  paper.addEventListener("click", () => {
    const computer = getComputerChoice();
    const val = game("paper", computer);
    getOutput(val);
  });
  scissors.addEventListener("click", () => {
    const computer = getComputerChoice();
    const val = game("scissors", computer);
    getOutput(val);
  });
};

const rock = document.getElementById("rock");
const paper = document.getElementById("paper");
const scissors = document.getElementById("scissors");

const getComputerChoice = () => {
  const choices = ["rock", "paper", "scissors"];
  const randomNumber = Math.floor(Math.random() * choices.length);
  return choices[randomNumber];
};

const getOutput = val => {
  if (val === uScore) {
    userScore.innerHTML = uScore;
    return (result.innerHTML = "<p>User wins!</p>");
  }
  if (val === cScore) {
    computerScore.innerHTML = cScore;
    return (result.innerHTML = "<p>Computer wins!</p>");
  }
  if (val === "draw") {
    return (result.innerHTML = "<p>It was a draw!</p>");
  }
};

play();
